import React, { Fragment, useEffect } from 'react';
import ReactCountryFlag from 'react-country-flag';

import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { fetchTrip } from '../actions';

import OverlappingFriends from '../components/trips/OverlappingFriends.react';
import Spinner from '../components/Spinner.react';

const TripDetail = ({ activeUser, match, trips, cities, users, fetchTrip }) => {
  const tripId = parseInt(match.params.id, 10);

  // Clone the trip
  let trip = trips.all[tripId];
  if (trip) {
    trip = {
      ...trip,
      city: cities[trip.city],
      matches: trip.matches.map(match => {
        const [uId, start, end] = match.split('::');
        return {
          ...users.all[uId],
          start_date: start,
          end_date: end
        };
      })
    };
    if (trip.user !== activeUser) {
      return <Redirect to="/404" />;
    }
  }

  useEffect(() => {
    // Get it from the API if:
    //   - it is not currently fetching trips
    //   - we don't have the trip in the store already
    if (!trips.isFetching && !trip) {
      fetchTrip(tripId);
    }
  }, []);

  const user = users.all[activeUser];

  return (
    <Fragment>
      <MainPanel isFetching={trips.isFetching} user={user} trip={trip} />
    </Fragment>
  );
};

const MainPanel = ({ isFetching, user, trip }) => {
  if (isFetching || !trip) {
    return <Spinner />;
  }

  trip.matches = [
    {
      start_date: '2018-12-12',
      end_date: '2019-05-01',
      first_name: 'Sam',
      have_requested_friend: false,
      id: 753072689,
      is_friend: true,
      is_pending_friend: false,
      is_you: false,
      last_name: 'Hunt',
      photo_url: null,
      username: null
    },
    {
      start_date: '2019-03-22',
      end_date: '2019-04-01',
      first_name: 'Old',
      have_requested_friend: false,
      id: 753072111,
      is_friend: true,
      is_pending_friend: false,
      is_you: false,
      last_name: 'Mate',
      photo_url: null,
      username: 'oldmate0'
    }
  ];
  return (
    <Fragment>
      <h2 className="uk-flex uk-flex-middle uk-flex-center">
        <ReactCountryFlag code={trip.city.country_code} svg />
        <span style={{ paddingLeft: 6 }}>{trip.city.name.toUpperCase()}</span>
      </h2>

      <div className="uk-flex trip-actions uk-flex-around">
        <button onClick={null} className="uk-button uk-button-default">
          <span className="icono-sliders icon-sm" /> EDIT
        </button>
        <button onClick={null} className="uk-button uk-button-default">
          <span className="icono-trash icon-sm" /> DELETE
        </button>
      </div>

      <br />

      <h5>YOUR DETAILS</h5>
      <OverlappingFriends {...trip} user={user} />
    </Fragment>
  );
};

const mapStateToProps = ({ activeUser, trips, users, cities }) => ({
  activeUser,
  trips,
  users,
  cities: cities.all
});

export default connect(
  mapStateToProps,
  {
    fetchTrip
  }
)(TripDetail);
